<template>
  <div v-if="posts.length">
    <div v-for="post in posts" :key="post.id">
      <h2>{{ post.title }}</h2>
      <p>{{ post.content }}</p>
    </div>
    <button @click="handleClick">Click me</button>
  </div>
  <div v-else>
    <h2>Posts loading...</h2>
  </div>
</template>

<script>
export default {
  data() {
    return {
      posts: [],
    };
  },
  mounted() {
    setTimeout(async () => {
      try {
        const res = await fetch("http://localhost:3000/posts");
        const data = await res.json();
        this.posts = data;
      } catch (err) {
        console.log(err.message);
      }
    }, 1000);
  },
  methods: {
    handleClick() {
      this.posts = [
        ...this.posts,
        {
          id: 3,
          title: "Third Content",
          content: "Nothing much going on here",
        }
      ];
    },
  },
  watch: {
    posts(newVal, oldVal) {
      console.log(oldVal, newVal);
    }
  },
};
</script>

